@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Metal+Mania");
@import url("https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap");
@font-face {
  font-family: "Booter - Zero Zero";
  src: url("../fonts/zero_zero.woff") format("woff"), url("../fonts/zero_zero.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bubblegum Sans', cursive;
  src: url("../fonts/Bubblegum_Sans.woff") format("woff");
  font-weight: normal;
}
body {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: Raleway;
    color: black;
    background-image: url("../images/Sky2.jpg");
    background-size: cover;
  }
  
  html, body {
    margin: 0;
    padding: 0;
    min-height: 100%;
  }
  
  .nav ul {
    font-family: "Bubblegum Sans";
    margin: 0;
  }
  
  .nav li {
    display: inline;
  }
  
  .nav a {
    display: inline-block;
    padding: .5em;
    color: white;
    text-decoration: none;
  }
  
  .main-nav {
    font-family: "Bubblegum Sans";
    text-align: center;
    font-size: 1.1em;
    font-weight: lighter;
    border-bottom: 3px solid rgba(255, 255, 255, 0.3);
  }
  
  .main-nav li {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: "Bubblegum Sans";
    color: #777;
    padding: 0 5%;
    font-size: 1.5em;
  }
  
  .nav a:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }
  
  .main-header {
    background-color: rgba(216, 91, 189, 0.74);
    background-image: url("../images/Sky3.jpg");
    background-blend-mode: multiply;
    background-size: cover;
  }
  
  .site-name {
    text-align: center;
    margin: 0;
    font-size: 4em;
    font-family: "Booter - Zero Zero";
    font-weight: normal;
    color: white;
  }
  
  .site-name-large {
    font-size: 8em;
  }
  
  .container {
    background-size: auto;
    margin: 0 auto;
    margin-top: 0;
    padding: 0 1.5em;
  }
  
  .content-section {
    margin: 0;
    margin-bottom: 0;
  }
  
  .section-header {
    font-weight: normal;
    color: rgb(29, 11, 99);
    text-align: center;
    font-size: 2.5em;
    font-family: "Bubblegum Sans";
  }
  
  .about-site-image {
    float: left;
    height: 220px;
    width: 220px;
    margin: 20px;
    border-radius: 30%;
  }
  .home-page-image {
    float: left;
    height: 200px;
    width: 200px;
    margin: 20px;
    border-radius: 50%;
  }
  
  .main-footer {
    background-color: #56CCF2;
    color: white;
    padding: .25em 0;
    margin: 0 auto;
    margin-bottom: 0;
    max-height: 90px;
  }
  
  .main-footer-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  
  .main-footer-container ul {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    text-align: end;
  }
  
  .footer-nav li {
    padding: 0 .5em;
  }
  
  .footer-nav img {
    width: 30px;
    height: 30px;
  }
  
  .btn {
    text-align: center;
    vertical-align: middle;
    padding: .67em .67em;
    cursor: pointer;
  }
  
  .btn-header {
    margin: .5em 15% 2em 15%;
    color: white;
    border: 2px solid #2D9CDB;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 0;
    font-size: 1.5em;
    font-weight: lighter;
    padding-left: 2em;
    padding-right: 2em;
  }
  
  .btn-header:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }
  
  .btn-play {
    display: block;
    margin: 0 auto;
    color: #2D9CDB;
    font-size: 4em;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    padding: 0;
    text-align: center;
  }
  
  .btn-primary {
    color: white;
    background-color: #56CCF2;
    border: none;
    border-radius: .3em;
    font-weight: bold;
  }
  
  .btn-primary:hover {
    background-color: #2D9CDB;
  }
  
  .tour-row {
    border-bottom: 1px solid black;
    padding-bottom: 1em;
    margin-bottom: 1em;
  }
  
  .tour-row:last-child {
    border: none;
  }
  
  .tour-item {
    display: inline-block;
    padding-right: .5em;
  }
  
  .tour-date {
    color: #555;
    width: 11%;
    font-weight: bold;
  }
  
  .tour-city {
    width: 24%;
  }
  
  .tour-arena {
    width: 42%;
  }
  
  .tour-btn {
    max-width: 19%;
  }
  
  .shop-item {
    margin: 30px;
  }
  
  .shop-item-title {
    display: block;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 1.5em;
    color: #333;
    margin-bottom: 15px;
  }
  
  .shop-item-image {
    box-sizing: border-box;
    border-radius: 8%;
  }
  .phone-stands {
    height: 25em;
  }
  .flowers{
    height: 15em;
  }
  
  .shop-item-details {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 5px;
  }
  
  .shop-item-price {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    color: #333;
  }
  
  .shop-items {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  
  .cart-header {
    font-weight: bold;
    font-size: 1.25em;
    color: #333;
  }
  
  .cart-column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-bottom: 1px solid black;
    margin-right: 1.5em;
    padding-bottom: 10px;
    margin-top: 10px;
  }
  
  .cart-row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  
  .cart-item {
    width: 45%;
  }
  
  .cart-price {
    width: 20%;
    font-size: 1.2em;
    color: #333;
  }
  
  .cart-quantity {
    width: 35%;
  }
  
  .cart-item-title {
    color: #333;
    margin-left: .5em;
    font-size: 1.2em;
  }
  
  .cart-item-image {
    width: 75px;
    height: auto;
    border-radius: 10px;
  }
  
  .btn-danger {
    color: white;
    background-color: #EB5757;
    border: none;
    border-radius: .3em;
    font-weight: bold;
  }
  
  .btn-danger:hover {
    background-color: #CC4C4C;
  }
  
  .cart-quantity-input {
    height: 34px;
    width: 50px;
    border-radius: 5px;
    border: 1px solid #56CCF2;
    background-color: #eee;
    color: #333;
    padding: 0;
    text-align: center;
    font-size: 1.2em;
    margin-right: 25px;
  }
  
  .cart-row:last-child {
    border-bottom: 1px solid black;
  }
  
  .cart-row:last-child .cart-column {
    border: none;
  }
  
  .cart-total {
    text-align: end;
    margin-top: 10px;
    margin-right: 10px;
  }
  
  .cart-total-title {
    font-weight: bold;
    font-size: 1.5em;
    color: black;
    margin-right: 20px;
  }
  
  .cart-total-price {
    color: #333;
    font-size: 1.1em;
  }
  
  .btn-purchase {
    display: block;
    margin: 40px auto 80px auto;
    font-size: 1.75em;
  }
  